import Vue from 'vue'
import App from './App.vue'
import VueLazyload from 'vue-lazyload'

const loadimage = 'https://dummyimage.com/400x400/ffffff/000baa.png&text=+Loading...'
const errorimage = 'https://dummyimage.com/400x400/ffffff/000baa.png&text=+Loading...'

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: errorimage,
  loading: loadimage,
  attempt: 1
})

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')
